import React from 'react';
import { motion } from 'framer-motion';
import { MapPin, Calendar } from 'lucide-react';
import { Experience } from '../../types';

interface ExperienceCardProps {
  experience: Experience;
  index: number;
}

const ExperienceCard: React.FC<ExperienceCardProps> = ({ experience, index }) => {
  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr);
    return date.toLocaleDateString('en-US', { year: 'numeric', month: 'short' });
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5, delay: index * 0.1 }}
      viewport={{ once: true }}
      className="bg-white dark:bg-gray-800 rounded-lg p-6 shadow-md hover:shadow-lg transition-shadow"
    >
      <div className="flex flex-col sm:flex-row sm:items-start mb-4">
        <div className="flex-grow">
          <h3 className="text-xl font-bold text-gray-900 dark:text-white mb-1">
            {experience.title}
          </h3>
          
          <h4 className="text-lg font-medium text-blue-600 dark:text-blue-400 mb-1">
            {experience.company}
          </h4>
          
          <div className="flex flex-col sm:flex-row sm:items-center text-sm text-gray-600 dark:text-gray-400 mb-4">
            <span className="flex items-center mb-2 sm:mb-0 sm:mr-4">
              <MapPin className="w-4 h-4 mr-1" />
              {experience.location}
            </span>
            
            <span className="flex items-center">
              <Calendar className="w-4 h-4 mr-1" />
              {formatDate(experience.startDate)} - {experience.endDate === 'Present' ? 'Present' : formatDate(experience.endDate)}
            </span>
          </div>
        </div>
      </div>
      
      <p className="text-gray-600 dark:text-gray-400 mb-4">
        {experience.description}
      </p>
      
      <div className="flex flex-wrap gap-2">
        {experience.tags.map((tag) => (
          <span 
            key={tag} 
            className="text-xs font-medium bg-gray-100 text-gray-800 dark:bg-gray-700 dark:text-gray-200 px-2.5 py-0.5 rounded"
          >
            {tag}
          </span>
        ))}
      </div>
    </motion.div>
  );
};

export default ExperienceCard;