import { logEvent } from '../lib/analytics';

export const handleResumeDownload = () => {
  // Track the download event
  logEvent('Resume', 'download', 'Resume PDF Download');
  
  // The resume URL - update this with your actual resume URL
  const resumeUrl = 'https://priyanshuchawda.github.io/portfolio-v1/Priyanshu_Chawda_Resume.pdf';
  
  // Create a temporary link element
  const link = document.createElement('a');
  link.href = resumeUrl;
  link.download = 'Priyanshu_Chawda_Resume.pdf';
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
};
